import './App.css';
import { useHistory, useLocation, Route, Switch, Redirect } from 'react-router-dom';
import Header from './Header';
import Home from './Home';
import About from './About';
import Projects from './Projects';
import Experience from './Experience';

function App() {
  const history = useHistory();
  const location = useLocation();

  // Conditionally apply a class to the container based on the current path
  const name = location.pathname === "/home" ? "container opacity-little" : "container opacity-lots";

  // Redirect function
  const redirectToHome = () => {
    history.push("/home");
  };

  return (
    <div>
      <Header />
      <div className={name} id="container">
        <Switch>
          {/* Define Routes for each page */}
          <Route path="/home" component={Home} />
          <Route path="/about" component={About} />
          <Route path="/projects" component={Projects} />
          <Route path="/experience" component={Experience} />

          {/* Catch-all route to redirect to /home */}
          <Route path="*">
            <Redirect to="/home" />
          </Route>
        </Switch>
      </div>
    </div>
  );
}

export default App;
